.works {
	background-color: rgb(53, 47, 48);
	display: flex;
	align-items: center;
	justify-content: center;
  // for the arrow images
  position: relative;

  .arrow{
    height: 60px;
    position: absolute;
    cursor: pointer;

    &.left{
      left: 100px;
      transform: rotateY(180deg);
    }
    &.right{
      right: 100px;
      // transform: rotateY(180deg);
    }
  }

	.slider {
		height: 350px;
		display: flex;
		position: absolute;
		left: 0;
    transition: all 1s ease-out;

		.container {
			width: 100vw;
			display: flex;
			align-items: center;
			justify-content: center;

			.item {
				width: 700px;
				height: 100%;
				background-color: white;
				border-radius: 20px;
				display: flex;
				align-items: center;
				justify-content: center;

				.left {
					flex: 4;
					height: 80%;
					display: flex;
					align-items: center;
					justify-content: center;
					// background-color: blue;

					.leftContainer {
						width: 90%;
						height: 70%;
						display: flex;
						flex-direction: column;
						justify-content: space-between;

						.imgContainer {
							width: 40px;
							height: 40px;
							border-radius: 50%;
							background-color: salmon;
							display: flex;
							align-items: center;
							justify-content: center;

							img {
								width: 25px;
							}
						}
            h2{
              font-style: 20px;
            }
            p{
              font-style: 13px;
            }
            span{
              font-style: 12px;
              font-weight: bold;
              text-decoration: underline;
              cursor: pointer;
            }
					}
				}
				.right {
					flex: 8;
					height: 100%;
					background-color: orange;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          
          img{
            width: 100%;
            height: 100%;
            // transform: rotate(-10deg);
          }
				}
			}
		}
	}
}
